.pdf-container {
    height: 100vh;
    width: 100%;
    position: relative; /* Position elements within this container */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pdf-iframe {
    width: 100%;
    height: 80vh;
    border: none;
}

.download-container {
    position: absolute;
    top: 10px;
    right: 10px;
}

.btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #357edd;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.btn:hover {
    background-color: #0056b3;
}

